import { Region } from './../../providers/region/region.model';
import { RegionProvider } from './../../providers/region/region';
import { Category } from './../../providers/category/category.model';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController, AlertController, Events } from 'ionic-angular';
import { CategoryProvider } from '../../providers/category/category';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';

@IonicPage()
@Component({
  selector: 'page-filtrar-modal',
  templateUrl: 'filtrar-modal.html',
  providers: [ CategoryProvider ]
})
export class FiltrarModalPage {

  /**
   * Categorias do sistema
   * 
   */
  public categories: Category[] = [];

  /**
   * Regiões do sistema
   * 
   */
  public regions: Region[] = [];

  /**
   * Regiao ativa
   * 
   */
  public activedRegion = '';

  /**
   * Indica se é a primeira vez organizando o feed
   *
   */
  public firstTime = false;

  /**
   * Categorias no item ALL
   * 
   */
  public inAllCategories: Category[] = [];

  /**
   * Método construtor
   * 
   * @param navCtrl 
   * @param navParams 
   * @param categoryProvider 
   * @param viewCtrl 
   */
  constructor(  public navCtrl: NavController, 
                public navParams: NavParams,
                modalCtrl: ModalController,
                public loadingCtrl: LoadingController,
                public regionProvider: RegionProvider,
                public events: Events,
                public categoryProvider: CategoryProvider, 
                public viewCtrl: ViewController,
                public alertCtrl: AlertController ) {
      
    // Verifica se é a primeira vez
    this.firstTime = !localStorage.getItem( 'already-opened' ) ? true : false;
    if ( localStorage.getItem('categories') ) localStorage.setItem( 'is_open', 'false' );
  
    if( this.firstTime ) {
      const modal = modalCtrl.create( 'BoasVindasPage' );
      modal.present();
    }

    // Se inscreve no evento
    this.events.subscribe( 'update:categories', () => {
      this.ionViewWillEnter();
    });
  }
  
  /**
   * Lista as catgorias salvas no sistema
   * 
   */
  ionViewWillEnter(){

    // Pega as categorias
    this.categories = this.categoryProvider.listFromStorage();
    this.regions    = this.regionProvider.listFromStorage();

    // Pega a regiao ativa
    const region       = this.regionProvider.getActivedRegion();
    this.activedRegion = region ? region.sigla : null;
  }

  /**
   * Muda o status do actived
   * 
   * 
   * @param index 
   */
  toggledActived( index ) {
    this.categories[index].actived = !this.categories[index].actived;
  }

  /**
   * Reordena a lista de acordo com a posicao atual
   * 
   * @param indexes 
   */
  reorderList( indexes ) {
    let element = this.categories[indexes.from];
    this.categories.splice(indexes.from, 1);
    this.categories.splice(indexes.to, 0, element);
  }

  /**
   * Quando for sair da página
   * 
   */
  salvarAlteracoes(){

    // Mostra o loading
    const loading = this.loadingCtrl.create( { content: 'Salvando itens...' } );
    loading.present();

    // Seta a ordem
    for( let index in this.categories ) {
      this.categories[index].order = index;
    }

    // Salva no localstorage
    this.categoryProvider.saveCategoriesOnStore( this.categories );

    // Esconde o loading
    loading.dismiss();
  }

  /**
   * Abre a lista de veiculos para a categoria
   * 
   * @param category 
   */
  openList( category ) {
    this.navCtrl.push( 'VeiculosPage', { category } );
  }

  /**
   * Mostra mensagem de alerta
   * 
   */
  showAlert() {
    let alert = this.alertCtrl.create({
      title: 'Informe uma categoria!',
      subTitle: 'Por gentileza, informe ao menos uma categoria para ter acesso ás notícias!',
      buttons: ['OK']
    });
    alert.present();
  }

  /**
   * Fecha o modal
   * 
   */
  close() {

    // Busca alguma categoria ativa
    let aux   = false;
    let inAll = 0;
    for( let category of this.categories ) {
      if( category.actived == true ) {
        aux = true;
      }
      if ( category.showInAll ) {
        inAll++;
      }
    }

    // verifica se existe algum para tudo
    if ( inAll == 0 && aux ) {
      for( let c in this.categories ) {
        if( this.categories[c].actived == true ) {
          this.categories[c].showInAll = true;
        }
      }
    }

    // Executa a ação
    if( aux ) {   
      localStorage.setItem( 'already-opened', 'true' );
      this.salvarAlteracoes();
      if ( this.firstTime ) {
        this.navCtrl.setRoot( 'FeedPage' );
      } else this.viewCtrl.dismiss();
    } else {
      this.showAlert();
    }
  }

  /**
   * Quando a regiao for alterada
   * 
   */
  changedRegion() {
    this.regionProvider.setActivedRegion( this.activedRegion );
  }
}

